
export class SandboxContainer {
    divHost: HTMLElement;
    div: HTMLDivElement;
    divMenu: HTMLDivElement;
    divContainer: HTMLDivElement;

    orderedList: HTMLOListElement;
    constructor(id: string, title: string = 'sandbox') {

        this.initDiv(id);
        this.initMenu(title);
    }
    public initDiv(id: string) {
        this.divHost = document.getElementById(id) as HTMLElement;

        this.div = document.createElement('div');
        this.div.className = 'main-body'
        this.divHost.appendChild(this.div);

        this.divMenu = document.createElement('div');
        this.divMenu.className = 'menu-left';
        // this.divMenu.style.width = '200px';
        this.divMenu.style.paddingLeft = '0px';
        this.divMenu.style.background = '#333';
        this.div.appendChild(this.divMenu);

        this.divContainer = document.createElement('div');
        this.divContainer.className = 'container-sandbox';
        this.divContainer.id = 'container-sandbox';
        this.div.appendChild(this.divContainer);     
        this.divContainer.style.width = '100%';
        this.divContainer.style.height = '100%';
        this.divContainer.style.background = '#999';

    }
    public initMenu(t: string) {
        const title = document.createElement('h1');
        title.className ='menu-left-title';
        title.style.paddingLeft = '10px';
        title.textContent = t;
        this.divMenu.appendChild(title);

        const nav = document.createElement('nav');
        nav.className = 'left-menu menu-left-font';

        this.orderedList  = document.createElement('ol');
        this.orderedList.style.paddingLeft = '25px';
        this.orderedList.className = 'menu-left-font';
        this.divMenu.appendChild(this.orderedList);

    }
    public addMenu(title: string, fn: () => void ) {
        let list = document.createElement('li');
        this.orderedList.appendChild(list);
        list.className = 'sub-menu-left-padding-top';
        list.innerHTML = `<a>${title}</a>`;
        list.onclick = () => fn(); 
        
    }
    public destroy(){
        while(this.divHost.lastChild) {
            this.divHost.removeChild(this.divHost.lastChild)
        }
    }

}
